/*css-reset*/
*{box-sizing: border-box;}
*::before, *::after{box-sizing: border-box;}
*{
    margin: 0;
    padding: 0;
}
ul,ol{list-style: none;}
a{color:inherit; text-decoration: none;}
img{max-width: 100%; max-height: 100%;}

/*style*/
body{
    /* background: #eee; */
    min-width: 100vw;
    min-height: 100vh;
    background: url('./images/bg.jpg');
}

.globalHeader{
    margin: 20px;
}

@media(min-width:500px){
    .globalHeader{
        margin: 60px 0 100px;
    }
}
.searchForm{
    display: flex;
    justify-content: space-between;
}
/*宽度500px以下的不管 500px以上的进行{}里的操作*/
@media (min-width:500px){
    .searchForm{
        max-width: 400px;
        margin-left: auto;
        margin-right: auto;
    }
}
.searchForm > input{
    width: 100%;
    margin-right: 10px;
    height: 40px;
    padding: 0 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}
.searchForm > button{
    white-space: nowrap;
    padding: 0 28px;
    border: none;
    background: #1E9CAD;
    border-radius: 4px;
    color: white;
    font-size: 16px;
}

.globalMain{
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
}

.siteList{
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
@media(min-width:500px){
    .siteList{
        margin-left: 0;
        margin-right: -25px;
        justify-content: flex-start;
    }
}
.siteList > li{
    margin-bottom: 10px;
}
@media(min-width:500px){
    .siteList > li{
        margin-right: 25px;
    }
}
.siteList .site{
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding:20px 0;
    position: relative;
    cursor: pointer;/*手显示*/
}
.siteList .site > .logo{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 64px;
    text-transform: uppercase;
}
.siteList .site > .link{
    font-size: 14px;
    margin-top: 5px;
}
.siteList .site > .close{
    position: absolute;
    right: 10px;
    top: 5px;
    display: none;
}

.siteList .site:hover > .close{
    display: block;
    cursor: default;
}
.siteList .addButton{
    border: 1px solid #ddd;
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding:20px 0;
    background: white;
}

.siteList .addButton .icon{
    width: 64px;
    height: 64px;
}

.siteList .addButton .text{
    font-size: 14px;
    margin-top: 5px;
}

.siteList .addButton .icon-wrapper{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}